 * @Description: 表格
-->
<template>
  <el-container>
    <el-header> {{ title }} </el-header>
    <el-main>
      <el-radio-group v-model="radio1" @input="changeViewMode">
        <el-radio-button :label="1">列表</el-radio-button>
        <el-radio-button :label="2">图标</el-radio-button>
      </el-radio-group>
      <el-table
        :data="tableData"
        style="width: 100%"
        @selection-change="handleSelectionChange"
        v-if="isListView"
        ref="multipleTable"
        :row-style="{ height: '80px' }"
      >
        <el-table-column type="selection" width="55"> </el-table-column>
        <el-table-column prop="fileName" label="文件名" width="180">
          <template slot-scope="scope">
            <FileIcon
              :fileName="scope.row.fileName"
              :fileType="scope.row.fileType"
              :textStyle="textStyle"
            />
            <!-- {{ scope.row.fileName }} -->
          </template>
        </el-table-column>
        <el-table-column prop="fileType" label="类型" width="180">
        </el-table-column>
        <el-table-column prop="fileSize" label="大小" width="180">
          <template slot-scope="scope">
            {{ formatFileSize(scope.row.fileSize) }}
          </template>
        </el-table-column>
        <el-table-column prop="createAt" label="上传时间"> </el-table-column>
      </el-table>
      <!-- 图标视图 -->
      <div class="icons-view" v-else>
        <el-checkbox-group v-model="checkedFiles">
          <el-checkbox
            v-for="file in tableData"
            :label="file"
            :key="file.index"
            class="file-box"
          >
            <FileIcon
              :fileName="file.fileName"
              :fileType="file.fileType"
              :iconStyle="iconModel"
              :textStyle="iconModelTextStyle"
              :elementStyle="iconModelElementStyle"
            />
          </el-checkbox>
        </el-checkbox-group>
      </div>

      <!-- 懒加载表格 -->
      <div class="lazy_table">
        <el-table
          :data="tableData"
          row-key="id"
          :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
          :row-style="{ height: '80px' }"
        >
          <el-table-column type="index" :index="indexMethod" width="100">
          </el-table-column>
          <el-table-column prop="fileName" label="文件名" width="240">
            <template slot-scope="scope">
              <FileIcon
                :fileName="scope.row.fileName"
                :fileType="scope.row.fileType"
                :textStyle="textStyle"
              />
            </template>
          </el-table-column>
          <el-table-column prop="fileType" label="类型" width="180">
          </el-table-column>
          <el-table-column prop="fileSize" label="大小" width="180">
            <template slot-scope="scope">
              {{ formatFileSize(scope.row.fileSize) }}
            </template>
          </el-table-column>
          <el-table-column prop="createAt" label="上传时间"> </el-table-column>
        </el-table>
      </div>
    </el-main>
  </el-container>
</template>

<script>
export default {
  name: "TableTemplate",
  props: {},
  components: {
    FileIcon: () => import("@/components/_Common/FileIcon.vue"),
  },
  computed: {},
  data() {
    return {
      radio1: 1,
      isListView: true,
      title: "表格",
      textStyle: "margin-left:8px",
      iconModel: "font-size:60px;display:inline-block;margin-bottom:4px", //图标模式图标样式
      iconModelTextStyle:
        "display:block;width: 120px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;", //图标模式文件名样式
      iconModelElementStyle: "text-align:center", //图标模式组件样式
      tableData: [
        {
          id: "1",
          fileName: "布偶猫撒娇",
          fileType: "jpg",
          fileSize: "21394",
          createAt: "2022-10-31 18:32:12",
        },
        {
          id: "2",
          fileName: "养猫指南",
          fileType: "pdf",
          fileSize: "913924",
          createAt: "2011-05-24 18:32:12",
          children: [
            {
              id: 21,
              fileName: "布偶指南",
              fileType: "pdf",
              fileSize: "41924",
              createAt: "2011-05-24 18:32:12",
            },
          ],
        },
      ],
      checkedFiles: [], //选中的文档
    };
  },
  async mounted() {},
  methods: {
    indexMethod(i) {
      let index = i + 1;
      index = index.toString().padStart(4, "0");
      return index;
    },
    changeViewMode(value) {
      if (value === 1) {
        this.isListView = true;
        this.$nextTick(() => {
          if (this.checkedFiles.length > 0) {
            this.checkedFiles.forEach((row) => {
              this.$refs.multipleTable.toggleRowSelection(
                this.tableData.find((item) => {
                  return row.id === item.id;
                }),
                true
              );
            });
          }
        });
      } else {
        this.isListView = false;
      }
    },
    handleSelectionChange(val) {
      this.checkedFiles = val;
    },
  },
};
</script>

<style scoped>
.lazy_table {
  margin-top: 20px;
}
.icons-view {
  display: flex;
  flex-wrap: wrap;
}
.file-box {
  text-align: center;
  margin: 20px;
  width: 120px;
  /* .svg-icon {
      font-size: 60px;
    } */
}

::v-deep .icons-view .el-checkbox__input {
  display: none;
  /* position: absolute;
  top: -15px;
  left: -15px; */
}

::v-deep .icons-view .el-checkbox__label {
  display: flex;
  align-items: center;
  width: 128px;
  height: 100px;
}

/* 设置选中后的背景颜色 */
::v-deep .icons-view .el-checkbox__input.is-checked + .el-checkbox__label {
  background: #87ceeb66;
  border-radius: 8px;
}
</style>